.eko-cart {
  color: white;
  background-color: #0083c9d0;
  padding: 32px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 300px;
}

.eko-cart-add-button {
  margin-left: 32px;
  cursor: pointer;
}

.eko-cart-toggle-button {
  background: none;
  outline: none;
  border: none;
  font-weight: 600;
  cursor: pointer;
  align-self: self-start;
  padding: 0;
  color: white;
  align-self: flex-end;
}

.eko-cart-closed {
  padding: 32px;
  width: 300px;
}

.eko-cart-closed > button {
  color: black;
}

#eko-btn-empty-cart {
  margin-left: 10px;
  width: 200px;
  height: 30px;
  border-radius: 10px;
  border-color: white;
}
